export default {
  props: {
    data: {
      type: Object,
      required: true
    },
    minified: {
      type: Boolean,
      default: false
    },
    round: {
      type: String,
      required: true
    },
    activeParty: {
      default: null,
      validator (val) {
        return !val || typeof val === 'string'
      }
    }
  },
  methods: {
    isPartyDeactivated (person) {
      return this.activeParty && this.activeParty !== person.party
    },
    councilorLink (person) {
      return {
        name: 'round-councilor-councilor',
        params: {
          round: this.round,
          councilor: person.id
        }
      }
    }
  }
}
